<template>
    <div id="app">
        <div class="register-container">
            <h2>创建账号</h2>
            <form @submit.prevent="register">
                <div class="input-field">
                    <i class="fa fa-user" aria-hidden="true"></i>
                    <input type="text" placeholder="用户名" v-model="username" required>
                </div>
                <div class="input-field">
                    <i class="fa fa-lock" aria-hidden="true"></i>
                    <input type="password" placeholder="密码" v-model="password" required>
                </div>
                <div class="input-field">
                    <i class="fa fa-lock" aria-hidden="true"></i>
                    <input type="password" placeholder="确认密码" v-model="confirmPassword" required>
                </div>
                <button type="submit" class="register-button">创建账号</button>
            </form>
            <p @click="toLogin()">返回登录</p>
        </div>
    </div>
</template>
  
<script>
export default {
    data() {
        return {
            username: '',
            password: '',
            confirmPassword: ''
        };
    },
    methods: {
        register() {
            // 检查密码和确认密码是否一致
            if (this.password !== this.confirmPassword) {
                alert('密码和确认密码不一致！');
                return;
            }

            let url = '/car/register';
            let data = {
                userName: this.username,
                password: this.password
            };

            fetch(url, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(data)
            })
                .then(response => response.text())
                .then(data => {
                    if (data === 'User created successfully') {
                        console.log('注册成功');
                        alert('注册成功！');
                        // 在这里添加注册成功后的逻辑
                        this.toLogin();
                    } else {
                        console.log('注册失败: ', data);
                        alert('注册失败！');
                        // 在这里添加注册失败后的逻辑
                    }
                })
                .catch((error) => {
                    console.error('Error:', error);
                });
        },
        toLogin() {
            this.$router.push('/login');
        }
    }
};

</script>
  
<style scoped>
h2 {
    margin-bottom: 3rem;
}

#app {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
}

.register-container {
    width: 37.5rem;
    height: 45rem;
    padding: 5rem;
    background-color: #fff;
    border-radius: 1.25rem;
    box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
}

.input-field {
    display: flex;
    align-items: center;
    margin-bottom: 1.875rem;
}

.input-field i {
    margin-right: 1.25rem;
}

.input-field input {
    width: 100%;
    padding: 1.25rem;
    border: none;
    border-radius: 0.625rem;
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.1);
}

.register-button {
    width: 100%;
    padding: 1.25rem;
    border: none;
    border-radius: 0.625rem;
    background-color: #4caf50;
    color: #fff;
    cursor: pointer;
}
</style>
  